import { mapState, mapActions, mapMutations } from 'vuex'
import {regionData, provinceAndCityDataPlus, regionDataPlus, CodeToText, TextToCode } from 'element-china-area-data'
export default {
  data() {
    //  写跟进验证（当跟进方式为到访时拜访人员为必填）
    var checkContact = (rule, value, callback) => {
      if(this.follow_form.followType == '到访' & !value){
        callback(new Error('跟进方式为到访时请填写拜访人'));
      }else{
        callback();
      }
    };
    //  验证手机号
    var checkPhone = function(rule, value, callback){
      let reg = /^1[0-9]{10}$/;
      if(reg.test(value)){
        callback()
      }else{
        callback(new Error('手机号错误!'))
      }
    };
    return {
      dialogTransferVisible: false,  // 转给其他人
      staff: '',      //转给他人：员工
      formTransfer: {
        checkbox1: false,   //  是否保留为团队成员
        checkbox2: false,   //  是否转移线索
        checkbox3: false,   //  是否转移合同
        checkbox4: false,   //  是否转移机会
      }, //  转给他人checkbox


/**----------------------------------客户信息  客户全景------------------------------------- */
      customer: {}, //  客户信息
      tabName: 'tab1',   //  标签页

/**-------------------------------------------基本信息-------------------------------------------- */
      customerFieldList: [],    //  客户字段集合
      customerEdit: {},         //  客户编辑信息
      customer_rules: {},       //  客户字段校验
      isEdit: false, // 是否编辑

/**---------------------------------------附件-------------------------------------------- */
      dialogAnnexVisible: false,  
      annexList: [],    //  附件列表
      files: [], // 上传文件列表
      annexRemarks: '',       //  备注
      
/**-------------------------------设置团队成员权限--------------------------- */
      isOwner: false,   //  是否是拥有者
      addMemberInput: '', //  新增团队成员
      allStaff: [],    //  所有员工列表

/**---------------------------------写跟进------------------------------------ */
      follows: [],  //  跟进列表
      follow_type_list: [], //  跟进方式
      follow_status_list: [], //  跟进状态
      contact_list: [], //  当前客户所有联系人列表
      followRules: {
        followType: [
          { required: true, message: '请选择跟进方式', trigger: 'change' }
        ],
        followTime: [
          { type: 'date', required: true, message: '请选择跟进时间', trigger: 'change' }
        ],
        followContent: [
          { required: true, message: '请填写跟进内容', trigger: 'blur' }
        ],
        followStatus: [
          { required: true, message: '请选择跟进状态', trigger: 'change' }
        ],
        contactCname: [
          { validator: checkContact, trigger: 'change' }
        ]
      },      //  写跟进验证规则
      follow_form: {},

/**--------------------------------联系人---------------------------- */
      dialogContactdVisible: false,     //  新增联系人模态框
      contactsAddLabel: [],   //  新增联系人label
      contactsAdd:{tags: []},         //  联系人新增对象
      imgSrc: '', //  联系人头像
      inputVisible: false,    //  是否显示编辑tag的input框
      inputValue: '',         //  新增标签input值
      contacts_rules: {
        customerName: [
          { required: true, message: '请选择客户', trigger: 'change' },
        ],
        name: [
          { required: true, message: '请输入联系人姓名', trigger: 'blur' },
        ],
        mobilePhone: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }
        ]
      }, //  新增联系人验证规则




      query: {},                  //  url参数
      customer_id:'',//客户主键
      customer_basic_info:'',//客户基本信息
      customer_basic:{lastFollowTime: ''},//动态展示客户基本信息字段
      contacts_title_size:'',//联系人标题+数量
      contacts_list:[],//联系人集合
      dynamic_form:[],//动态展示字段的表单 ：联系人
      savaflag:'',//保存标识符
      operateLog :[],//操作日志
      customer_input_list:[],

      
      is_show: true,                                  //  跟进进度样式显示
      formBasicInfo: {

      },                                              //  基本信息表单
      options: regionData,                            //  地区总数据
      selectedOptions: [],                            //  地区结果
      dynamicTags: ['标签一', '标签二', '标签三'],      //  标签
      tableData1: [],                                 //  机会表格数据
      tableData2: [],                                 //  合同表格数据
      tableData3: [{name: 123,address: 123}],         //  附件表格数据
      dialogContactsVisible: false,                   //  添加联系人模态框
      dialogChanceVisible: false,                     //  添加机会模态框
      dialogContractVisible: false,                   //  添加合同模态框
      // dialogSeasVisible: false,                       //  转至公海模态框
      dialogOthersVisible: false,                         //  转移给他人模态框
      dialogEditVisible: false,                         //  编辑客戶模态框
      dialogSettingsVisible: false,                         //  权限设置模态框
      dialogFolowVisible: false,                      //  写跟进模态框
      memberList: [],                                                              //   团队成员
    }
  },
  activated(){
    this.query = this.$router.currentRoute.query;
    //  获取客户详情页面信息
    this.getCustomerDetail()
  },
  

  methods: {
    //  切换tab
    chooseTab(tab, event){
      if(this[this.tabName]){
        this[this.tabName]()
      }
    },

/**-------------------------------------------------获取联系人----------------------------------------- */
    getContacts(){
      this.getContactsAddLabel()
      this.$post("/crm/contactsDetailPR/queryOtherContacts", {id: this.customer.id}, (data) => {
        this.contacts_list = data.list
      })
    },
    getContactsAddLabel(){
      if(!this.contactsAddLabel.length){
        this.$post("/crm/extFieldPR/getField", {tableName: 'crm_contacts'}, (data) => {
          this.contactsAddLabel = data.list;
        })
      }
    },
    //  新增联系人按钮
    addContat() {
      this.dialogContactdVisible = true;
    },
    //  打开新增模态框
    openContactModal(){
      this.getContactsAddLabel()
    },
    //  关闭新增模态框，清除数据
    closeContactModal(){
      this.imgSrc = ''
      this.$refs.uploadInput.value = ''
      this.$refs.contactForm.resetFields();
    },
    //  打开input file
    chooseImg(){
      this.$refs.uploadInput.click()
    },
    //  选择图片
    pictureChange(e){
      if(e.target.files.length) this.imgSrc = e.target.files[0]
    },
    //  添加标签
    handleClose(tag) {
      this.contactsAdd.tags.splice(this.contactsAdd.tags.indexOf(tag), 1);
    },
    //  添加标签
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    //  添加标签
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.contactsAdd.tags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = '';
    },
    //  保存联系人
    contactsSave(){
      this.$refs.contactForm.validate((valid) => {
          if (valid) {
              this.contactsAdd.customerId = this.customer.id
              this.contactsAdd.customerName = this.customer.name
              let params = new FormData()
              params.append('avatar', this.imgSrc)
              params.append('contactsAdd', JSON.stringify(this.contactsAdd))
                  this.$post("/crm/contactsPR/addCrmContacts", params, (data) => {
                  this.$message.success('操作成功！');
                  this.getContacts()
                  this.dialogContactdVisible = false;
              })
          }
      });
    },
    


/*--------------------------------------------转给他人功能--------------------------------------------*/
    // 打开转给他人弹框
    turnOther(){
      this.getAllStaff()
      this.dialogTransferVisible = true
    },
    // 转给他人确定按钮
    turn_other_sava(){
      if(!this.staff){
        this.$message.error("请选择新拥有者！")
        return
      }
      let list = [this.customer.id]
      let params = {
        list: list,
        ownerId: this.staff,
        ifLeft: this.formTransfer.checkbox1
      }
      this.$post("/crm/customerPR/turnOther", params, (data) => {
        this.dialogTransferVisible = false
        this.$message.success("转移成功！")
        this.$router.back(-1)
      })
    },
    //  转移至公海
    turnSea(command){
      this._confirm('确定要将【' + this.customer.name + '】转至公海客户池吗？转移成功后，此客户属于公共资源，原拥有者不能再维护跟进。', () => {
        this.$post('/crm/customerPR/moveToSea', {"list": [this.customer.id]}, (data) => {
          this.$message.success("转移成功！")
          this.$router.back(-1)
        })
      })
    },

    //  删除客户
    delCustomer(){
      this._confirm('确定删除吗?', () => {
        this.$post('/crm/customerPR/deleteCrmCustomer', {"list": [this.customer.id]}, (data) => {
          this.$message.success("删除成功！")
          this.$router.back(-1)
        })
      })
    },


/**------------------------------------------------附件----------------------------------------------------- */
    //  获取附件列表
    getAttachs(){
      this.$post("/crm/getAttachs", {parentId: this.customer.id}, (data) => {
        this.annexList = data.list
      })
    },
    //  删除附件
    delAnnex(id, index){
      this._confirm('确定删除吗?', () => {
        this.$post("/crm/deleteFile", {ids: [id]}, (data) => {
          this.$message.success("删除成功")
          this.annexList.splice(index, 1)
        })
      })
    },
    //  下载附件
    downLoadAnnex(id){
      console.log('下载')
    },
    //  关闭附件模态框
    closeAnnexModal(){
      this.annexRemarks = '',
      this.files = []
    },
    //  打开选择文件
    chooseAnnex(){
      this.$refs.annexInput.click()
    },
    //  选择文件
    changeFile(e){
      let files = this.$refs.annexInput.files
      for(let i=0; i< files.length; i++){
        this.files.push(files[i])
      }
      this.$refs.annexInput.value = ''
    },
    //  删除待上传文件
    delFile(index){
      this.files.splice(index, 1)
    },
    //  上传附件
    annexSave(){
      if(!this.files.length){
        this.$message.error("请选择上传文件！")
        return
      }
      let params = new FormData()
      this.files.map((item, index) => {
        params.append("file" + index, item)
      })
      params.append("remark", this.annexRemarks)
      params.append("parentId", this.customer.id)
      this.$post("/crm/upfile2", params, (data) => {
        this.$message.success("上传成功！")
        this.dialogAnnexVisible = false
        this.getAttachs()
      })
    },

/**---------------------------------------------基本信息----------------------------------------------------- */
    //  基本信息
    tab2(){
      if(!this.customerFieldList.length){
        this.$post("/crm/extFieldPR/getField", {tableName: 'crm_customer'}, (data) => {
          this.customerFieldList = data.list
        })
      }
    },
    //  编辑客户
    editCustomer(){
      this.tabName = 'tab2'
      this.isEdit = true
      this.customer_rules['phone'] = { required: true, validator: this.checkPhone, trigger: 'blur' }
      this.tab2()
    },
    //  取消编辑
    cancelEdit(){
      this.isEdit = false
      this.$refs.customerEdit.resetFields()
    },
    //  保存编辑
    customerSave(){
      this.$post("/crm/customerPR/updateCrmCustomer", this.customerEdit, (data) => {
        this.$message.success("修改成功！")
        this.isEdit = false
        this.customer = this.deepClone(this.customerEdit)
      })
    },





    //  获取客户详情信息
    getCustomerDetail(){
      this.$post("/crm/customerDetailPR/queryCrmCustomerForOne", {id: this.query.id}, (data) => {
        this.customer = data.customer
        this.follows = data.follows
        this.isOwner = data.isOwner
        this.customerEdit = this.deepClone(data.customer)
      })
    },

/**---------------------------------------------写跟进--------------------------------------------- */
    //  获取数据字典
    getDict(){
      if(this.follow_type_list.length) return
      this.$post("/crm/getDict", {list: ['CRM-GJFS', 'CRM-KH-GJZT']}, (data) => {
        this.follow_type_list = data['CRM-GJFS']
        this.follow_status_list = data['CRM-KH-GJZT']
      })
    },
    //  是否显示跟进日期
    showDate(index){
      if(index > 0){
        let date1 = this.formatDate(new Date(this.follows[index-1].followTime))
        let date2 = this.formatDate(new Date(this.follows[index].followTime))
        if(date1 == date2){
          return false
        }
      }
      return true
    },
    //  打开写跟进模态框
    openFollowModal(){
      this.follow_form.followTime = new Date()
      this.getAllStaff()
      this.getDict()
      this.getContacts()
      console.log(this.follow_form)
    },
    //  关闭写跟进模态框
    closeFollowModal(){
      
    },
    //  写跟进提交
    followSave(){
      this.$refs.follow_form.validate((valid) => {
        if (valid) {
          if(this.follow_form.contactCname){
            this.follow_form.contactId = this.follow_form.contactCname.split("/")[0]
            this.follow_form.contactCname = this.follow_form.contactCname.split("/")[1]
          }
          if(this.follow_form.copyToUserCname){
            this.follow_form.copyToUserId = this.follow_form.copyToUserCname.split("/")[0]
            this.follow_form.copyToUserCname = this.follow_form.copyToUserCname.split("/")[1]
          }
          this.follow_form.parentId = this.customer.id
          this.follow_form.moduleName = "crm_customer"
          this.$post("/crm/followPR/addOne", {follow: this.follow_form}, (data) => {
            this.$message.success("添加成功")
            this.follows.unshift(data.follow)
            this.dialogFolowVisible = false
            this.follow_form = {}
          })
        }
      });
    },









/**----------------------------------------------------设置团队成员权限-------------------------------------------- */
    //  加载所有员工
    getAllStaff() {
      if(!this.allStaff.length){
        this.$post("/crm/getAllStaff", {}, (data) => {
          this.allStaff = data.list
          this.contact_list = data.list
        })
      }
    },
    //  获取团队成员
    getMembers(){
      this.dialogSettingsVisible = true
      this.$post("/crm/customerMemberPR/getMembers", {"customerId": this.query.id}, (data) => {
        this.memberList = data.members
      })
    },
    //  添加团队成员
    addMember(){
      if(this.addMemberInput){
        let params = {
          customerId: this.query.id,
          userId: this.addMemberInput
        }
        this.$post("/crm/customerMemberPR/addMember", params, (data) => {
          this.memberList.unshift(data.member)
        })
      }else{
        this.$message.error("请输入员工姓名或工号")
      }
    },
    //  删除团队成员
    deleteMember(id, index){
      console.log(id)
      this.$post("/crm/customerMemberPR/delMember", {id: id}, (data) => {
        this.memberList.splice(index, 1)
        this.$message.success("删除团队成员成功！")
      })
    },
    //  修改团队成员权限
    updateMember(type, index, id){
      let params = {
        id: id,
        canModify: type
      }
      this.memberList[index].is_modify = type
      this.$post("/crm/customerMemberPR/updateMember", params, (data) => {
        this.memberList[index].is_modify = !type
        this.$message.success("权限修改成功！")
      })
    },
  }
}



